import {
    HostBinding, Input, Optional, Self, Component,
    OnInit, ViewChild, OnDestroy, forwardRef
} from '@angular/core';

import { NgControl, ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

import { MatFormFieldControl, MatRadioGroup } from "@angular/material";

import { Subject } from 'rxjs';

import { coerceBooleanProperty } from '@angular/cdk/coercion';

@Component({
    selector: 'polp-radio-group-field',
    templateUrl: 'radio-group-field.component.html',
    styleUrls: ['./radio-group-field.component.scss'],
    providers: [
        {
            provide: MatFormFieldControl,
            useExisting: RadioGroupFieldControl
        },
        {
            provide: NG_VALUE_ACCESSOR,
            multi: true,
            useExisting: forwardRef(() => RadioGroupFieldControl)
        }
    ],
})
export class RadioGroupFieldControl implements MatFormFieldControl<any>, OnInit, OnDestroy, ControlValueAccessor {

    // View child
    @ViewChild(MatRadioGroup) radioGroup: MatRadioGroup;

    // Value
    stateChanges = new Subject<void>();

    // Unique id
    static nextId = 0;
    @HostBinding() id = `radio-group-field-control-${RadioGroupFieldControl.nextId++}`;

    // Focused 
    focused = false;

    // Error state
    errorState = false;

    // control type
    controlType = 'radio-group-field';

    // Described 
    @HostBinding('attr.aria-describedby') describedBy = '';

    // Specific to radio buttons
    @Input() radioOptions: Array<{
        value: any;
        text: string;
    }>;

    ngControl: NgControl = null;

    constructor() { }

    // Value
    @Input()
    get value(): any {
        return this.radioGroup.value;
    }
    set value(v: any) {
        this.radioGroup.value = v;
        this.stateChanges.next();
    }

    // Place holder
    @Input()
    get placeholder() {
        return this._placeholder;
    }
    set placeholder(plh) {
        this._placeholder = plh;
        this.stateChanges.next();
    }
    private _placeholder: string;

    // Never be empty
    get empty() {
        return false;
    }

    // should floating 
    @HostBinding('class.floating')
    get shouldLabelFloat() {
        return true;
    }

    // required 
    @Input()
    get required() {
        return this.radioGroup.required;
    }
    set required(req) {
        // todo:
        this.radioGroup.required = coerceBooleanProperty(req);
        this.stateChanges.next();
    }

    @Input()
    get disabled() {
        return this.radioGroup.disabled;
    }
    set disabled(dis) {
        this.radioGroup.disabled = coerceBooleanProperty(dis);
        this.stateChanges.next();
    }

    ngOnInit() {
    }

    // Override 
    ngOnDestroy() {
        this.stateChanges.complete();
    }

    // set described by ids
    setDescribedByIds(ids: string[]) {
        this.describedBy = ids.join(' ');
    }

    // onContainerClick(event: MouseEvent)
    onContainerClick(event: MouseEvent) {
        // todo:
    }

    // Control value accessor
    writeValue(value: any) {
        if (value !== undefined) {
            this.radioGroup.writeValue(value);
        }
    }

    // todo: When will this be invoked???
    registerOnChange(fn) {
        this.radioGroup.registerOnChange(fn);
    }

    registerOnTouched(fn) {
        this.radioGroup.registerOnTouched(fn);
    }

}
