import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatCheckboxChange } from '@angular/material';

import { HttpEventType } from '@angular/common/http';
import { Subscription } from 'rxjs';

import { guid } from '@polpware/fe-utilities';

import { IUploadFileService } from './upload-file.interface';

export interface IUploadFileInput {
    title: string;
    prelude: string;
    uploadService: IUploadFileService;
    uploadUrl: string;
    /** File extension that accepted, same as 'accept' of <input type="file" />. 
             By the default, it's set to 'image/*'.
    */
    accept: string;
    progressMode: 'determinate' | 'indeterminate';
    makeFormData: (file: File) => FormData;
    validateInput: (files: FileList) => string;
    closeOnSunccess: boolean;
}

class FileUploadModel {
    uuid: string;
    data: File;
    state: string;
    inProgress: boolean;
    progress: number;
    canRetry: boolean;
    canCancel: boolean;
    message: string;
    sub?: Subscription;
}

@Component({
    selector: 'plop-upload-file',
    templateUrl: './upload-file.component.html',
    styleUrls: ['./upload-file.component.scss']
})
export class UploadFileComponent implements OnInit {

    title: string;
    text: string;
    accept: string;
    mode: string;
    error: string;

    files: Array<FileUploadModel> = [];
    private responses: Array<any> = [];

    constructor(public readonly dialogRef: MatDialogRef<UploadFileComponent>,
        @Inject(MAT_DIALOG_DATA) public readonly data: IUploadFileInput) {

        this.title = this.data.title || '';
        this.accept = this.data.accept || '*/';
        this.text = this.data.prelude || '';
        this.mode = this.data.progressMode || 'indeterminate';
    }

    public get isCloseDisabled(): boolean {
        return this.files.some(x => x.inProgress);
    }

    close() {
        this.dialogRef.close(this.responses);
    }

    ngOnInit() {
    }

    startUpload() {
        const fileUpload = document.getElementById('polpFileUpload') as HTMLInputElement;
        fileUpload.onchange = () => {
            // Validate inputs
            const message = this.data.validateInput(fileUpload.files);
            if (message) {
                this.error = message;
                return;
            }

            for (let index = 0; index < fileUpload.files.length; index++) {
                const file = fileUpload.files[index];
                this.files.push({
                    uuid: guid(),
                    data: file,
                    state: 'in',
                    inProgress: false,
                    progress: 0,
                    canRetry: false,
                    canCancel: true,
                    message: ''
                });
            }
            this.uploadFiles();
        };
        fileUpload.click();
    }

    cancelFile(file: FileUploadModel) {
        file.sub.unsubscribe();
        this.removeFileFromArray(file);
    }

    retryFile(file: FileUploadModel) {
        this.uploadFile(file);
        file.canRetry = false;
    }

    private postUploadFile(file: FileUploadModel, body: any) {
        this.responses.push({
            uuid: file.uuid,
            body: body
        });

        // If this is the last one file which has been uploaded
        if (this.data.closeOnSunccess) {
            if (this.files.length === this.responses.length) {
                // Schedule to close
                setTimeout(() => {
                    this.close();
                });
            }
        }
    }

    private uploadFile(file: FileUploadModel) {
        file.inProgress = true;
        file.sub = this.data.uploadService.upload(file.data,
            this.data.uploadUrl, this.data.makeFormData)
            .subscribe((value) => {
                switch (value.eventType) {
                    case HttpEventType.UploadProgress:
                        file.progress = value.percent;
                        break;
                    case HttpEventType.Response:
                        file.message = 'Upload Ok.';
                        file.inProgress = false;
                        file.canCancel = false;
                        file.canRetry = false;

                        this.postUploadFile(file, value.body);
                        break;
                }

            }, (error) => {
                file.inProgress = false;
                file.canRetry = true;
                file.message = `upload failure.`;
            });
    }

    private uploadFiles() {
        const fileUpload = document.getElementById('polpFileUpload') as HTMLInputElement;
        fileUpload.value = '';

        this.files.forEach(file => {
            this.uploadFile(file);
        });
    }

    private removeFileFromArray(file: FileUploadModel) {
        const index = this.files.indexOf(file);
        if (index > -1) {
            this.files.splice(index, 1);
        }
    }

}    
