import {
    ModuleWithProviders, NgModule,
    Optional, SkipSelf
} from '@angular/core';

import { CommonModule } from '@angular/common';

import {
    MatProgressSpinnerModule,
    MatDialogModule,
    MatButtonModule,
    MatIconModule,
    MatRadioModule,
    MatProgressBarModule,
    MatListModule
} from '@angular/material';

import {
    ReactiveFormsModule,
    FormsModule
} from '@angular/forms';

import { TagInputModule } from 'ngx-chips';

import { AutosizeModule } from 'ngx-autosize';

import { PolpMdIndicatorModal } from './indicator/modal.component';
import { PolpMdSpinnerServiceImpl } from './services/spinner-service.impl';

import { RadioGroupFieldControl } from './radio-group-field/radio-group-field.component';

import { UploadFileComponent } from './upload-file/upload-file.component';

import { ConfirmDialogComponent } from './confirm-dialog/confirm-dialog.component';

import { SnackbarErrorComponent } from './snackbar/snackbar-error.component';
import { SnackbarInfoComponent } from './snackbar/snackbar-info.component';
import { SnackbarSuccessComponent } from './snackbar/snackbar-success.component';
import { SnackbarWarnComponent } from './snackbar/snackbar-warn.component';
import { MessageFormComponent } from './message-form/message-form.component';
import { AlertBoxComponent } from './alert-box/alert-box.component';

@NgModule({
    declarations: [
        PolpMdIndicatorModal,
        RadioGroupFieldControl,
        UploadFileComponent,
        ConfirmDialogComponent,

        SnackbarErrorComponent,
        SnackbarInfoComponent,
        SnackbarSuccessComponent,
        SnackbarWarnComponent,
        MessageFormComponent,
        AlertBoxComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,

        MatProgressSpinnerModule,
        MatDialogModule,
        MatButtonModule,
        MatIconModule,
        MatRadioModule,
        MatProgressBarModule,
        MatListModule,

        TagInputModule,
        AutosizeModule
    ],
    exports: [
        PolpMdIndicatorModal,
        RadioGroupFieldControl,
        UploadFileComponent,
        ConfirmDialogComponent,
        AlertBoxComponent
    ],
    entryComponents: [
        PolpMdIndicatorModal,
        UploadFileComponent,
        ConfirmDialogComponent,

        SnackbarErrorComponent,
        SnackbarInfoComponent,
        SnackbarSuccessComponent,
        SnackbarWarnComponent,

        MessageFormComponent
    ],
    providers: [
        PolpMdSpinnerServiceImpl
    ]
})
export class PolpMdComponentsModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: PolpMdComponentsModule
        };
    }

    constructor(@Optional() @SkipSelf() parentModule: PolpMdComponentsModule) {
        if (parentModule) {
            throw new Error(
                'PolpMdComponentsModule is already loaded. Import it in the AppModule only');
        }
    }
}
