import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatCheckboxChange } from '@angular/material';

export interface IMessageFormInput {
    title: string;
    message: string;
}

export interface IMessageFormExport {
    confirmed: boolean;
    message: string;
}

@Component({
    selector: 'polp-md-message-form',
    templateUrl: './message-form.component.html',
    styleUrls: ['./message-form.component.scss']
})
export class MessageFormComponent implements OnInit {

    public title: string;
    public messageBody: string;

    constructor(public dialogRef: MatDialogRef<MessageFormComponent>,
        @Inject(MAT_DIALOG_DATA) public data: IMessageFormInput) {

        data.title && (this.title = data.title);
        this.messageBody = data.message || '';
    }

    public get isSubmitDisabled(): boolean {
        return !this.messageBody;
    }


    ngOnInit() {
    }

    close() {
        this.dialogRef.close({
            confirmed: false,
            message: ''
        });
    }


    confirm() {
        this.dialogRef.close({
            confirmed: true,
            message: this.messageBody || ''
        });
    }

}
